var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("超人")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        超人是一个虚构人物，是DC Comics出版的美国漫画书中出现的超级英雄。由作家杰里·西格尔和艺术家乔·舒斯特创作的角色首次出现在1938年4月18日的动作漫画＃1中，标志着漫画书黄金时代的兴起。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        自从他成立以来，超人被描绘成一个英雄，起源于氪星球，并命名为Kal-El。作为一个婴儿，他被生物家庭Jor-El和Lara 送入地球，在Krypton在一场自然灾害中被摧毁之前不久。他的船降落在美国乡村; 他被农民乔纳森和玛莎肯特发现并收养在靠近Smallville小镇的地方，他将他命名为克拉克肯特。克拉克显示各种超人能力从一开始就作为一个年轻的男孩，如令人难以置信的力量和不透水的皮肤。他的养父母建议他利用自己的能力造福人类，并决定以犯罪者的身份打击犯罪。为了保护他的隐私，他变成了一件色彩缤纷的服装，并在​​打击犯罪时使用别名“超人”。克拉克肯特在他成年的生活中居住在美国虚构的大都市，他在那里担任“ 每日星球”的记者，在那里为人们掩盖自己。通常描绘配角 超人被描述为居住在大城市，如著名的爱的兴趣超人的Lois车道，好朋友超人，吉米·奥尔森和 每日星球主编佩里怀特。他有许多敌人，比如他的经典持久的大敌，天才发明家Lex Luthor。他是DC宇宙中许多其他超级英雄的朋友，如蝙蝠侠和神奇女侠。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        虽然超人不是第一个超级英雄角色，但他推广了超级英雄类型并定义了它的惯例。他仍然是有史以来 最畅销的漫画超级英雄并且作为最赚钱的特许经营之一，即使在漫画书之外也是如此。他一般被认为是有史以来最伟大的超级英雄/漫画人物。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260"}})],1),_c('br'),_c('h2',[_vm._v("创作起源")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        超人是由杰里西格尔和乔舒斯特创造的。最初遇到的一对二人在1932年在克利夫兰的一所高中遇到了他们对小说的共同爱好。西格尔渴望成为一名作家，舒斯特渴望成为一名插画家。西格尔写了业余科幻故事，他自己出版了一本名为“ 科幻小说：未来文明的先进卫士 ”的杂志。他的朋友舒斯特经常为他的作品提供插图。1933年1月，西格尔在他的杂志上发表了一篇题为“ 超人的统治 ” 的短篇小说。这个名义上的角色是一个名叫Bill Dunn的流浪汉，他被一个邪恶的科学家欺骗，用来吸食一种实验性药物。这种药物让Dunn具有思维阅读，精神控制和洞察力的能力。他恶意利用这些权力获取利润。娱乐，但随后药物消失，再次让他成为一个无能为力的流浪者。舒斯特提供插图，描绘邓恩是一个光头男人。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        西格尔和舒斯特转而制作漫画，专注于冒险和喜剧。他们想成为辛迪加的报纸创作者，所以他们向各种报纸编辑展示了自己的想法。然而，报纸编辑告诉他们，他们的想法不够耸人听闻。如果他们想制作一部成功的连环漫画，它必须比市场上任何其他东西都更具煽风性。这促使西格尔重新审视超人作为漫画角色。西格尔修改超人的力量，使他更加耸人听闻：像比尔·邓恩，超人的第二架原型机是被不道德的科学家给出对他的意志的力量，而是心灵的能力，他获得超人的力量和防弹皮肤。此外，这位新超人是一个打击犯罪的英雄而不是恶棍，因为西格尔指出，带有英雄主角的漫画往往更成功。在后来的几年里，西格尔曾经回忆起这位超人在某些面板上穿着“蝙蝠状”斗篷，但他和舒斯特一致同意那里没有服装，而且幸存的艺术作品中没有任何一种。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('br'),_c('h2',[_vm._v("影视作品及游戏")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"})])}
